body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.navbar {
    background: #a61733;
    box-shadow: 0 10px 10px rgba(0, 0,0.25);
    color: whitesmoke;
    position:fixed;
    width: 100%;
    z-index: 99;
    white-space: nowrap;
}



body {
    font-size: 24px;
}
.header {
    background-color: #ccc;
    position: relative;
    width: 100%;
}

h1 {
    text-align: center;
    font-size: 90px;
    font-weight: bold;
    padding: 90px 0 20px 0;
}

h2 {
    text-align: center;
    font-size: 25px;
    padding-bottom: 40px;
}
.card {
    width: 200px;
    height: 150px;
    margin: 15px 45px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    border: 3px solid white;
}

img {
    width:200px;
    height: 150px;
}
.card > .img-container {
    height: 100%;
    overflow: hidden;
    background: rgb(255, 255, 255);
}

.card > .img-container > img {
    height: 100%;
}

.card:hover {
    box-shadow: 8px 8px 20px rgb(56, 56, 56);
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
}

card:active {
    -webkit-transform: translate(1px, 1px);
            transform: translate(1px, 1px);
}
.footer {
    width: 100%;
    /* height: 80px; */
   
    position: relative;
    color: whitesmoke;
}


.logo {
    height: 50px;
    width: 60px;
}

.shadow1 {
    /* /* margin:40px; */
    background-color: rgb(68, 68, 68);
    box-shadow: 5px 5px 5px rgba(68, 68, 68, 0.6);
    -webkit-box-shadow: 5px 5px 5px rgba(68, 68, 68, 0.6);
    
}

.shadow1 .content {
    position: relative;
    /* padding: 100px; */
    background-color: #a61733;
}
.flex-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
